<template>
    <div id="table">
      <el-button type="primary" plain @click.native="openFormDialog"></el-button>
      <el-button type="primary" plain @click.native="deleteSelected"></el-button>
      <el-button type="primary" plain @click.native="openEditDialog"></el-button>
      <el-table
        :data="numberTypeList"
        highlight-current-row
        @current-change="handleCurrentChange"
        height="100%"
        border
        style="width: 100%"
        ref="singleTable"
        @row-click="setCurrent">
        <el-table-column
          prop="number"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="name"
          label="">
        </el-table-column>
      </el-table>
      <el-dialog :visible.sync="formDialogVisible" title="">
        <el-form :model="newNumberType">
          <el-form-item label="">
            <el-input v-model="newNumberType.number"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="newNumberType.name"></el-input>
          </el-form-item>
        </el-form>
        <span class="dialog-footer">
          <el-button @click="formDialogVisible = false"></el-button>
          <el-button type="primary" @click="addNumberType"></el-button>
        </span>
      </el-dialog>
    </div>
</template>

<style>
#table{
  height: 100vh;
}
</style>

<script>
export default {
  data() {
    return {
      numberTypeList: [],
      formDialogVisible: false,
      newNumberType:{
        number:'',
        name:'',
      },
      currentRow: null, // 
    }
  }, computed: {},
  methods: {
    openEditDialog() {
      if (!this.currentRow) {
        this.$message.warning('');
        return;
      }
      this.isEdit = true;
      this.newNumberType = {...this.currentRow};
      this.formDialogVisible = true;
    },
    async deleteSelected() {
      if (!this.currentRow) return;
      try {
        await this.$db.deleteNumberTypeById(this.currentRow.id); // id
        this.$message.success('');
        await this.getNumberTypeList(); // 
        this.currentRow = null; // 
        window.location.reload(); // 
      } catch (error) {
        this.$message.error(`${error.message}`);
      }
    },
    handleCurrentChange(row) {
      this.currentRow = row; //  currentRow
    },
    openFormDialog(){
      this.formDialogVisible = true
    },
    async getNumberTypeList() {
      try {
        this.$db.selectNumberTypeList(null).then((res) => {
          this.numberTypeList = res
        })
      } catch (error) {
        this.$message.error('' + error);
      }
    }, async addNumberType() {
      try {
        await this.$db.addNumberType(this.newNumberType);
        this.$message.success('');
        await this.getNumberTypeList();
      } catch (error) {
        this.$message.error('' + error);
      }
      this.formDialogVisible = false;
      window.location.reload(); // 
    }
  },
    created() {
    this.getNumberTypeList();
  }
}
</script>



