<template>
  <el-form ref="form" :model="form" label-width="80px">
    <el-form-item label="">
      <el-select v-model="form.type" placeholder="">
        <el-option :label="item.number+''+item.name" :value="item.number" v-for="item in numberTypeList"
                   :key="item._id">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="">
      <el-input
        style="width: 30%"
        placeholder=""
        v-model="form.corp">
      </el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input
        style="width: 30%"
        placeholder=""
        v-model="form.name">
      </el-input>
    </el-form-item>
    <el-form-item label="">
      <el-radio-group v-model="form.router">
        <el-radio label="0"></el-radio>
        <el-radio label="1"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="">
      <el-input
        style="width: 30%"
        placeholder=""
        :value="displaySerialNumber"
        :disabled="true">
      </el-input>
      <el-button @click="getSerialNumberMax"></el-button>
      <el-button @click="copyCorpName"></el-button>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit"></el-button>
      <el-button></el-button>
    </el-form-item>
  </el-form>
</template>
<script>

import common from '@/common/common.js'
export default {
  data() {
    return {
      form: {
        type: '',
        corp: 'HT',
        name: '',
        delivery: false,
        router: '0',
        serialNumber: null,
        desc: ''
      },
      numberTypeList: []
    }
  }, computed: {
    displaySerialNumber() {
      if (this.form.serialNumber === null) {
        return null;
      }
      const paddedSerialNumber = this.form.serialNumber.toString().padStart(4, '0');
      //  this.form.router  this.$router
      return this.form.router === '0'
        ? `${this.form.name}  ${this.form.type}${this.form.corp}${paddedSerialNumber}`
        : `${this.form.type}${this.form.corp}${paddedSerialNumber}  ${this.form.name}`;
    }
  },
  methods: {
    getNumberTypeList() {
      this.$db.selectNumberTypeList(null).then((res, error) => {
        this.numberTypeList = res
      }).catch((error) => {
        this.$message.error('' + error);
      })
    }, copyCorpName() {
      navigator.clipboard.writeText(this.corpName).then(() => {
        this.$message.success('');
      }).catch((error) => {
        this.$message.error('' + error);
      });
    },async getSerialNumberMax() {
        try {
          const type = this.form.type;
          const maxSerialNumber = await this.$db.selectSerialNumberMax(type)
          let newSerialNumber = Number(maxSerialNumber)+1;
          this.form.serialNumber = newSerialNumber;
          // 
          this.$forceUpdate();
          await navigator.clipboard.writeText(this.displaySerialNumber);
          // 
          this.$message.success('');
        }catch (error){
          this.$message.error('' + error);
        }
    },async onSubmit() {
      try{
        await this.$db.addNumberData(this.form)
        this.$message.success('');
      }catch (error){
        this.$message.error('' + error);
      }
    }
  },
  created() {
    this.getNumberTypeList()
  }
}
</script>



