<template>
    <el-container style="border: 1px solid #eee">
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <el-menu :default-openeds="['1', '2']" @select="handleMenuSelect"
                 :collapse-on-select="false"
        >
          <el-submenu index="1">
            <template slot="title"><i class="el-icon-message"></i></template>
            <el-menu-item index="DataForm"></el-menu-item>
            <el-menu-item index="config"></el-menu-item>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title"><i class="el-icon-menu"></i></template>
            <el-menu-item v-for="item in numberTypeList" :key="item.id" :index="item.number" @click="selectNumberData(item)">
              {{ item.number }}{{ item.name }}
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>

      <el-container>
        <el-main>
          <el-component :is="pageName" :key="number" :number="number"></el-component>
        </el-main>
      </el-container>
    </el-container>
</template>

<style>
.el-container{
  height: 100vh;
}
el-component {
  display: flex;
  flex-direction: column;
  height: 100%;
}
</style>

<script>
import Config from '@/components/Config.vue'
import DataForm from '@/components/DataForm.vue'
import NumberData from "@/components/NumberData.vue";


export default {
  data() {
    return {
      pageName:null,
      numberTypeList:[],
      number:'',
      currentActiveIndex: "" //  index
    }
  }, methods: {
    handleMenuSelect(index) {
      this.pageName = {
        config: Config,
        DataForm: DataForm,
        NumberData: NumberData
      }[index]
      console.log(this.pageName)
    }, async getNumberTypeList() {
      try {
        this.numberTypeList = await this.$db.selectNumberTypeList(null);
      } catch (error) {
        this.$message.error('' + error);
      }
    }, selectNumberData(item) {
      this.number = item.number; // ID
      this.currentActiveIndex = item.number; // 
      this.handleMenuSelect("NumberData"); // 
    },
  },created() {
    this.getNumberTypeList()
  }
}
</script>



