<template>
  <div id="table">
    <el-table
      :data="numberDataList"
      highlight-current-row
      height="100%"
      border
      style="width: 100%"
      ref="singleTable"
      >
      <el-table-column
        prop="type"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="corp"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="serialNumber"
        label=""
        width="180"
        :formatter="formatSerialNumber">
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        label=""
        width="180">
        <template slot-scope="scope">
          {{ getRowDisplaySerial(scope.row) }}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<style>
#table{
  height: 100vh;
}
</style>

<script>
export default {
  props: ['number'], //  typeId
  data() {
    return {
      numberDataList: [],
    }
  }, computed: {
  },
  methods: {
  formatSerialNumber(row, column, cellValue) {
    return cellValue.toString().padStart(4, '0');
  },
    getRowDisplaySerial(row) {
      const { name, corp, type, serialNumber, router } = row;
      return router === '0'
        ? `${name || ''}${corp || ''}${type || ''}${serialNumber || ''}`
        : `${corp || ''}${type || ''}${serialNumber || ''}${name || ''}`;
    },async selectNumberDataList(){
      try{
        const type = this.number;
        this.numberDataList = await this.$db.selectNumberDataList(type)
      }catch (error){
        this.$message.error('' + error);
      }
    }
  },
  created() {
    this.selectNumberDataList();
  }
}
</script>



